@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'Quicksand', sans-serif;
 
}
a{
  text-decoration: none;
}

.App{
  display: flex;
  flex-direction: column;
}
.booknow__container{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 24px;
    background-color: #be615a;
    height: 20rem;

}
.booknow__contentWrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    color: white !important;
    height: 100%;
}
.booknow__contentWrapper > h1{
    font-family: 'Great Vibes', cursive;
    font-weight: 200;
    font-size: 4rem;
    word-wrap: normal;
}
.booknow__contentWrapper > h6{
    font-weight: 200;
    font-size: 1.2rem;
    line-height: 1.5
}
.booknow__btn{
    margin-top: 18px;
    padding: 20px;
    border: 1px solid white;
    border-radius: 50px;
    width: 13rem;
    text-align: center;
    font-weight: 700;
    letter-spacing: 1.6px;
    transition: .1s;
    cursor: pointer;
}
.booknow__btn:hover{
    background-color: white;
    color: black;
    transition: .1s;
}
    
@media screen and(min-width:440px) and (max-width:478px) {
    .booknow__contentWrapper > h1{
        
        font-size: 2.8rem;
    }
}
@media screen and (max-width:435px) {
    .booknow__contentWrapper > h1{
        
        font-size: 2rem;
    }
}
.connect{
    padding:56px 0;
    text-align: center;
}

.connect__socialicon{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
}
.connect__socialicon > *{
    margin: 0 .5rem !important;
}
.footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px;
    background-color: #333333;
    color: white !important;
}
.footer > h5{
    font-weight: 100;
    font-size: 1.2rem;
}
@media screen and (max-width:1050px) {
    .footer{
        display: block;
        text-align: center;
    }
    
}
.accordian__container{
    display: flex;
    align-items: center;
    justify-content: center;
}
.accordian{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 250px;
}
.accordian__question{
    display: flex;
    justify-content: space-between;
    padding: 5px;
    align-items: center;
    
}
.answer{
    margin: 0 5rem;
    overflow-wrap: break-word;
    text-align: center;
}
.frequentlyContainer{
    display: flex;
    flex-direction: column;
}

.frequentlyHeading{
    text-align: center;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    margin-bottom: 2.5rem;
}
.frequentlyHeading > h1{
    color: rgb(89, 89, 89);
    line-height: 1.4;
    font-size: 22px;
    letter-spacing: 0.091em;
    overflow-wrap: break-word;
    font-weight: 500;
}
.frequentlyHeading >p{
    margin-top: 1rem;
    font-size: 20px;
    color: rgb(94, 94, 94);
    overflow-wrap: break-word;
    text-align: center;
}
/* font-family: 'Great Vibes', cursive; */

.header{
    display: flex;
    padding: 20px;
    justify-content: space-between;
    align-items: center;
    background-color: #333333;
    color: #a3a7a9;
    margin: auto 1rem;
}
.header >a >h1{
    font-family: 'Great Vibes', cursive;
    font-weight: 300;
    color: #a3a7a9;
 
}
.header__navmenu{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 10rem;
   
}
.header__navitems{
    padding: 0 20px;
    font-weight: 300;
    font-size: 19px;
    letter-spacing: 0.167em;
    color: #a3a7a9;
}
.header__navitems:hover{
    color: #b3755e;
}
.header__icons{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
}
.header__drawer{
    display: none;
}
.spacer{
    width: 100%;
    height: 7rem;
}
.drawerItemAccount{
    margin-left: 16px;
    margin-top: 10px;
    margin-bottom: 16px;
    color: #aaa;
}
/* //////////////////////////////////////////////////////////////////////////////////////////////////////////// */
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .header__navmenu{
        display: none;
    }
    .header >a >h1{
        font-size: 18px;
        margin: 0 5px;
     
    }

}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
    .header__navmenu{
        display: none;
    }
    
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
    .header__navmenu{
        display: none;
    }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
    .header{
        display: flex;
        padding: 20px;
        justify-content: space-between;
        align-items: center;
        background-color: #333333;
        color: #a3a7a9;
    }
    .header >a >h1{
        font-family: 'Great Vibes', cursive;
        font-weight: 300;
        color: #a3a7a9;
     
    }
    .header__navmenu{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-right: 10rem;
       
    }
    .header__navitems{
        padding: 0 20px;
        font-weight: 300;
        font-size: 19px;
        letter-spacing: 0.167em;
    }
    .header__navitems:hover{
        color: #b3755e;
    }
    .header__icons{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px;
    }
    .header__drawer{
        display: none;
    }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {

    .header{
        display: flex;
        padding: 20px;
        justify-content: space-between;
        align-items: center;
        background-color: #333333;
        color: #a3a7a9;
    }
    .header >a >h1{
        font-family: 'Great Vibes', cursive;
        font-weight: 300;
        color: #a3a7a9;
     
    }
    .header__navmenu{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-right: 10rem;
       
    }
    .header__navitems{
        padding: 0 20px;
        font-weight: 300;
        font-size: 19px;
        letter-spacing: 0.167em;
    }
    .header__navitems:hover{
        color: #b3755e;
    }
    .header__icons{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px;
    }
    .header__drawer{
        display: none;
    }

}
.Himage__container{
    background-image: url(//img1.wsimg.com/isteam/stock/upmAxdPJbBImZ3Aro/:/rs=w:1023,m);
    min-height: 75vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.split__container{
    display: flex;
    
}
.split__image{
   
    
    /* background-size: cover;
    background-position: center;
    background-repeat: no-repeat; */
    max-width: 50%;
    flex: .55 1;
}
.split__image > img{
    object-fit: contain;
    aspect-ratio: auto;
    width: 100%;
}
.split__info{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    text-align: center;
    flex: .5 1

}
.split__info > h5{
    font-size: 22px;
    line-height: 1.5;
    overflow-wrap: break-word;
    color: #5E5E5E ;
    font-weight: 200;
}
.split__info > h1{
    font-family: 'Great Vibes', cursive;
    color: #b3755e;
    font-weight: 100;
    font-size: 3rem;
}
.divider{
    height: 20px;
    color: #5E5E5E ;
}
@media screen and (max-width:1025px) {
    .split__container{
        display: block;
    }
    .split__image{
        max-width: 100%;
        flex: 0 1;
    }
    .split__image > img{
        aspect-ratio: inherit;
        object-fit: cover;
        height: 100%;
        
    }
}
.services__container{
    text-align: center;
    padding:  5rem
}
.AdditionalInfo{
    display: flex;
    margin-top: 2rem;
}
.services__container > h3{
    margin-bottom: 2rem;
    color: rgb(89, 89, 89);
    font-size: 1.8rem;
    font-weight: 100;
}
.about{
    margin: 5px 0;
}
.service__title{
    margin-bottom: 2rem;
    font-weight: 700;
    font-size: 1.3rem;
    color: rgb(27, 27, 27);
}
.service__subtitle{
    color: rgb(94, 94, 94);
    overflow-wrap: break-word;
    font-size: 1.1rem;
}
@media screen and (max-width:850px) {
    .AdditionalInfo{
        display: block;
    }
    .CompleteAssuredServices > h4{
        margin-top: 2rem;
    }
}
